import React, { useContext, useState } from 'react'
import AppContext from '../../contextAPI/AppContext'
import styles from './InterlinearLeaf.module.css'
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import WordDataTable from '../WordDataTable/WordDataTable'
import theme from '../../theme'
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
    closeButton: {
      position: 'absolute',
      right: theme.spacing(1),
      top: theme.spacing(1),
      color: theme.palette.grey[500],
    },
    dialog: {
      width: '75vw',
      height: '75vh'
    },
    definition: {
      fontWeight: 'bold'
    }
  });

const InterlinearLeaf = (props) => {
    const classes = useStyles();
    const receivedAppContext = useContext(AppContext);
    
    // This controls the popup for the word information
    const [ dialog, updateDialog ] = useState({open: false, content: {}})
    const newDialog = (obj) => {
        updateDialog(obj)
    }

    // Check if the leaf is a chapter or verse
    const chapter = (props.marks) ? props.marks.find(mark => mark.type === 'chapter') : undefined
    const verse = (props.marks) ? props.marks.find(mark => mark.type === 'verse') : undefined
    const bibleRef = (verse || chapter) ? props.marks.find(mark => mark.type === 'reference') : undefined

    //ClassNames Array
    const classNames = (verse !== undefined || chapter !== undefined) ? 
        [
            (chapter) ? styles.chapter : null, 
            (verse) ? styles.verse : null
        ]
        .join(' ') : styles.interlinearLeaf
    
    // If it's just a chapter/verse reference then return that leaf with a reference
    if (verse || chapter) {
        return <span id={`${bibleRef.data.book}-${bibleRef.data.chapter}-${bibleRef.data.verse}`} className={(classNames !== null) ? classNames : ''}  style={{display: (receivedAppContext.displayRef) ? '' : 'none'}}>{props.text}{(chapter)?'':'\u00A0'}</span>
    }
    
    // Get Original Language Word Data
    const word_metadata = (props.marks) ? props.marks.find(mark => mark.type === 'word_metadata') : undefined

    // If it's just a chapter/verse reference then return that leaf with a reference
    if (!word_metadata) {
        return <span className={(classNames !== null) ? classNames : ''}>{props.text}</span>
    }

    // Check for paragraph breaks in the text string
    let newLine = props.text.search('\n')
    // If there's a paragraph break, split the text string into two parts: text1 = word, text2 = paragraph break + white space (if any)
    const text1 = (newLine !== -1)? props.text.substring(0,newLine): null
    const text2 = (newLine !== -1)? props.text.substring(newLine) : null

    const dictionary = (receivedAppContext.version === 'GNA') ? require('../../data/strongsGreekDictionary.json') : (receivedAppContext.version === 'WLC') ? require('../../data/strongsHebrewDictionary.json') : null

    const strongsNumberOnly = (!word_metadata.data.StrongNumber) ? '-' : (word_metadata.data.StrongNumber !== '') ? word_metadata.data.StrongNumber.substring(1,5) : word_metadata.data.extendedStrongNumber.substring(1,5)
    const gloss = (word_metadata.data.ETCBCgloss === '[object marker]' || 
        word_metadata.data.ETCBCgloss === '[relative]' ||
        word_metadata.data.ETCBCgloss === '[interrogative]' ||
        word_metadata.data.ETCBCgloss === '[uncertain]' ) ? '-' : (receivedAppContext.language === 'GRK') ? word_metadata.data.IT : word_metadata.data.ETCBCgloss

    if (newLine > -1) {
        return (
            <React.Fragment>
                <span 
                    key={word_metadata.data.word_ID} 
                    className={styles.whitespace} 
                    onClick={() => newDialog({open: !dialog.open, content: Object.assign(word_metadata.data, {text: props.text})})}
                >
                    <span key={word_metadata.data.word_ID + 'text'} className={styles.interlinearinterlinear}>{text1}</span>
                    <span key={word_metadata.data.word_ID + 'strong'} className={styles.interlinearStrongs}>{strongsNumberOnly}</span>
                    <span key={word_metadata.data.word_ID + 'gloss'} className={styles.interlinearTranslation}>{gloss}</span>
                </span>
                {(dialog.open === true) ? (
                    <Dialog 
                    maxWidth='lg'
                        open={dialog.open} 
                        onClose={() => newDialog({open: false, content: {}})}>
                        <DialogTitle>{`${dialog.content.text} : ${(dialog.content.StrongNumber !== '') ? `Strong's ${(receivedAppContext.language === 'HBR') ? 'OT' : 'NT'} #${dialog.content.StrongNumber.substring(1,5)}` : `Strong's ${(receivedAppContext.language === 'HBR') ? 'OT' : 'NT'} #${dialog.content.extendedStrongNumber.substring(1,5)}`}`}</DialogTitle>
                        <DialogContent dividers>
                            <p className={classes.definition}>{`STRONG'S DEFINITION: `}<em>{(dictionary[dialog.content.StrongNumber]) ? dictionary[dialog.content.StrongNumber].strongs_def : 'N/A'}</em></p>
                            <WordDataTable />
                        </DialogContent>
                    </Dialog> 
                ) : null}
                    {(!text2)?(<div className={styles.lineBreak}/>):(
                    <React.Fragment>
                        <div className={styles.lineBreak}/>
                        <span key={word_metadata.data.word_ID} className={styles.whitespace}>
                            <span key={word_metadata.data.word_ID + 'text'} className={styles.interlinearinterlinear}>{text2}</span>
                            {/* <span key={word_metadata.data.word_ID + 'strong'} className={styles.interlinearStrongs}>TEST</span>
                            <span key={word_metadata.data.word_ID + 'gloss'} className={styles.interlinearTranslation}>TEST</span> */}
                        </span>
                    </React.Fragment>)}
            </React.Fragment>
        )
    } else {
        return (
                <span 
                    key={word_metadata.data.word_ID} 
                    className={(receivedAppContext.language === 'GRK') ? styles.interlinearLeafGreek : styles.interlinearLeafHebrew} 
                    onClick={() => newDialog({open: !dialog.open, content: Object.assign(word_metadata.data, {text: props.text})})}
                >
                    <span key={word_metadata.data.word_ID + 'text'} className={styles.interlinearinterlinear}>{props.text}</span>
                    <span key={word_metadata.data.word_ID + 'strong'} className={styles.interlinearStrongs}>{strongsNumberOnly}</span>
                    <span key={word_metadata.data.word_ID + 'gloss'} className={styles.interlinearTranslation}>{gloss}</span>
                    {(dialog.open === true) ? (
                        <Dialog 
                        maxWidth='lg'
                            open={dialog.open} 
                            onClose={() => newDialog({open: false, content: {}})}>
                            <DialogTitle>{`${dialog.content.text} : ${(dialog.content.StrongNumber !== '') ? `Strong's ${(receivedAppContext.language === 'HBR') ? 'OT' : 'NT'} #${dialog.content.StrongNumber.substring(1,5)}` : `Strong's ${(receivedAppContext.language === 'HBR') ? 'OT' : 'NT'} #${dialog.content.extendedStrongNumber.substring(1,5)}`}`}</DialogTitle>
                            <DialogContent dividers>
                                <p className={classes.definition}>{`STRONG'S DEFINITION:  `}<em>{(dictionary[dialog.content.StrongNumber]) ? dictionary[dialog.content.StrongNumber].strongs_def : 'N/A'}</em></p>
                                <WordDataTable content={dialog.content}/>
                            </DialogContent>
                        </Dialog> 
                    ) : null}
                </span>
        )
    }
}

export default InterlinearLeaf;