import React, { useState } from 'react'
import { Doughnut } from 'react-chartjs-2';
import { HorizontalBar } from 'react-chartjs-2';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';

import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
  // main: {
  //   display: 'flex',
  //   justifyContent: 'center',
  //   flexWrap: 'wrap',
  //   textAlign: 'center',
  //   marginBottom: '75px',
  //   maxHeight: 'fit-content',
  // },
  occurrences: {
    height: '300px',
    width: '100%',
    // margin: '0 auto',
    position: 'relative'
  },
  byBook: {
    height: '600px',
    width: '100%',
    // margin: '0 auto',
    position: 'relative'
  },
  chartholder: {
    position: 'relative',
    width: '100%',
    height: '100%',
    padding: '5px',
    // marginBottom: '50px'
  },
  blackButton: {
    color: 'black',
    backgroundColor: 'rgb(201, 201, 201)',
    borderColor: 'black',
    margin: '2px'
  },
  redButton: {
    color: '#d60000',
    backgroundColor: '#ffadad',
    borderColor: '#d60000',
    margin: '2px'
  },
  greenButton: {
    color: '#006400',
    backgroundColor: '#b9f8b9',
    borderColor: '#006400',
    margin: '2px'
  },
  blueButton: {
    color: 'darkblue',
    backgroundColor: 'rgb(142, 227, 255)',
    borderColor: 'darkblue',
    margin: '2px'
  },
  red: {
    color: '#d60000'
  },
  green: {
    color: '#006400'
  },
  blue: {
    color: 'darkblue'
  },
  selected: {
    fontWeight: 'bold',
    textDecoration: 'underline'
  },
  roles: {
    width: '100%',
    height: 'auto',
    display: 'flex',
    justifyContent: 'space-between',
    marginTop: '10px'
  },
  roleSelectButton: {
    padding: '2px'
  },
  showReferences: {
    margin:'5px',
    padding: '5px',
    textAlign: 'center'
  }
})

const VerbCharts = (props) => {
  const classes = useStyles()
  const [ refColor, updateRefColor ] = useState('')
  const [ showRefs, updateShowRefs ] = useState(false)
  const verbFile = require(`../../data/Verbs/${props.fileRef}.json`)

  // ------- OCCURRENCES DOUGHNUT CHART------- //
  const occurrenceData = {
      labels: [`GodVerb (${verbFile[props.verbRef].NumGodVerbs})`,`SharedVerb (${verbFile[props.verbRef].NumSharedVerbs})` ,`OthersVerb (${verbFile[props.verbRef].NumOthersVerbs})`],
      datasets: [
      {
          data: [verbFile[props.verbRef].NumGodVerbs, verbFile[props.verbRef].NumSharedVerbs, verbFile[props.verbRef].NumOthersVerbs],
          backgroundColor: [
              "#ffadad",
              "rgb(174, 145, 209)",
              "rgb(142, 227, 255)"
          ],
          hoverBackgroundColor: [
              "#d60000",
              "rgb(111, 28, 212)",
              "darkblue"
          ]
      }] 
  };
  const occurrenceOptions = {
    title: {
      display: true,
      text: 'Occurrences',
      fontSize: 16
    },
    legend: {
      position: 'top'
    },
    maintainAspectRatio: false,
    responsive: true,
    layout: {
      padding: {
        left: 10,
        right: 10,
        top: 0,
        bottom: 50
      }
    },
    legend: {
      position: 'top',
      labels: {
        fontSize: 9,
        boxWidth: 40
      }
      // display: false
    },
  }
  
  // -------BOOK BAR CHART------- //
  const books = Object.keys(verbFile[props.verbRef].bookCounts)
  const VerbByBookData = () => {
    let data = {
      GodVerb: [],
      SharedVerb: [],
      OthersVerb: []
    }
    books.forEach(book => {
      data.GodVerb.push(verbFile[props.verbRef].bookCounts[book].GodVerbs)
      data.SharedVerb.push(verbFile[props.verbRef].bookCounts[book].SharedVerbs)
      data.OthersVerb.push(verbFile[props.verbRef].bookCounts[book].OthersVerbs)
      console.log(book, data)
    })
    return data
  }
  
  const bookData = {
      labels: books,
      datasets: [
          {
              label: 'GodVerbs',
              backgroundColor: '#ffadad',
              hoverBackgroundColor: '#d60000',
              data: VerbByBookData().GodVerb
          },
          {
              label: 'SharedVerbs',
              backgroundColor: 'rgb(174, 145, 209)',
              hoverBackgroundColor: 'rgb(111, 28, 212)',
              data: VerbByBookData().SharedVerb
          },
          {
              label: 'Others',
              backgroundColor: 'rgb(142, 227, 255)',
              hoverBackgroundColor: 'darkblue',
              data: VerbByBookData().OthersVerb
          }
      ]    
  };

  const HBarHeight = bookData.labels.length * 3

  const bookDataOptions = {
      title: {
          display: true,
          text: 'Occurrences per book',
          fontSize: 16
      },
      legend: {
          position: 'top'
      },
      scales: {
        xAxes: [{ stacked: true }],
        yAxes: [{ stacked: true }],
      },
      maintainAspectRatio: false,
      responsive: true
  }

  // ------- OCCURRENCES DOUGHNUT CHART------- //

// group the references into colors
const groupedReferencesByColor = () => {
  const all = verbFile[props.verbRef].GodRefs
  const black = verbFile[props.verbRef].GodRefs.filter(ref => ref.color === 'black')
  const red = verbFile[props.verbRef].GodRefs.filter(ref => ref.color === 'red')
  const green = verbFile[props.verbRef].GodRefs.filter(ref => ref.color === 'green')
  const blue = verbFile[props.verbRef].GodRefs.filter(ref => ref.color === 'blue')
  return {
    black,
    red,
    green,
    blue,
    all
  }
}
// count groups
const countGroupedRefs = () => {
  return {
    black: groupedReferencesByColor().black.length,
    red: groupedReferencesByColor().red.length,
    green: groupedReferencesByColor().green.length,
    blue: groupedReferencesByColor().blue.length
  }
}

  const SourceRoleData = {
    labels: [`Narrator`,`God` ,`Lead Character(s)`, `Other Character(s)`],
    datasets: [
    {
        data: [countGroupedRefs().black, countGroupedRefs().red, countGroupedRefs().green, countGroupedRefs().blue],
        backgroundColor: [
          'grey',
          '#ffadad',
          '#b9f8b9',
          'rgb(142, 227, 255)',
          ],
        hoverBackgroundColor: [
          'black',
          '#d60000',
          '#006400',
          'darkblue',
          ]
    }]    
};
const SourceRoleOptions = {
  title: {
    display: true,
    text: 'Source Role Comparison',
    fontSize: 16
  },
  legend: {
    position: 'top',
  },
  maintainAspectRatio: false,
  responsive: true,
  layout: {
    padding: {
      left: 10,
      right: 10,
      top: 0,
      bottom: 50
    }
  },
  legend: {
    position: 'top',
    labels: {
      fontSize: 9,
      boxWidth: 40
    }
    // display: false
  },
}

  return (
  <div className={classes.main}>
      <Divider />
      <div className={classes.occurrences}>
        <div className={classes.chartholder}>
          <Doughnut 
              data={occurrenceData} 
              options={occurrenceOptions}
              height={10}
              width={10}
          />
        </div>
      </div>
      <Divider />
      <div className={classes.byBook}>
        <div className={classes.chartholder}>
          <HorizontalBar 
              data={bookData}  
              width={50}
              height={HBarHeight}
              options={bookDataOptions}
          />
        </div>
      </div>
      <Divider />
      <div className={classes.occurrences}>
        <div className={classes.chartholder}>
          <Doughnut 
              data={SourceRoleData} 
              options={SourceRoleOptions}
              height={10}
              width={10}
          />
        </div>
      </div>
      <Divider />
      <div className={classes.showReferences}>
        <Button color='primary' variant={(showRefs) ? "outlined" : "contained"} onClick={() => updateShowRefs(!showRefs)}>{(showRefs) ? "Hide References" : "Show References"}</Button>
        {(showRefs) ? (
          <div className={classes.roles}>
            <span className={`${classes.black} ${classes.roleSelectButton} ${(refColor === 'all') && classes.selected}`} onClick={() => updateRefColor('all')}>All</span>
            <span className={`${classes.black} ${classes.roleSelectButton} ${(refColor === 'black') && classes.selected}`} onClick={() => updateRefColor('black')}>Black</span>
            <span className={`${classes.red} ${classes.roleSelectButton} ${(refColor === 'red') && classes.selected}`} onClick={() => updateRefColor('red')}>Red</span>
            <span className={`${classes.green} ${classes.roleSelectButton} ${(refColor === 'green') && classes.selected}`} onClick={() => updateRefColor('green')}>Green</span>
            <span className={`${classes.blue} ${classes.roleSelectButton} ${(refColor === 'blue') && classes.selected}`} onClick={() => updateRefColor('blue')}>Blue</span>
            
          </div>
        ) : null}
      </div>
      <Divider />
      {(refColor !== '' && showRefs) ? groupedReferencesByColor()[refColor].map(refButton => <Button size="small" className={classes[`${refButton.color}Button`]} key={refButton.ref}>{refButton.ref}</Button>) : null}
  </div>
  )
}

export default VerbCharts;