import React from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import VerbWordDataTable from '../../components/WordDataTable/VerbWordDataTable'
import VerbViewLogo from '../../images/VerbViewLogo.png'
import VerbCharts from '../../components/VerbView/VerbCharts'
import Typography from '@material-ui/core/Typography';

import { makeStyles } from '@material-ui/core/styles';
  
const useStyles = makeStyles(theme => ({
  typography: {
    padding: theme.spacing(2),
    fontSize: 'small',
    color: 'white'
  },
  definition: {
    fontWeight: 'bold'
  },
  dialogRef: {
    color: 'grey',
    fontSize: '80%',
    textAlign: "center",
    marginBottom: '-15px'
  },
  dialogHeader: {
      display: 'flex',
      justifyContent: 'space-between',
      marginBottom: '-10px'
  },
  logo: {
    height: '30px',
    width: 'auto'
  }
}))

const getVerbRef = (ref) => {
  if (ref.length === 4) {
    return ref
  } else if (ref.length === 3) {
    return `${ref.substring(0,1)}0${ref.substring(1,3)}`
  } else if (ref.length === 2) {
    return `${ref.substring(0,1)}00${ref.substring(1,2)}`
  }
}

const VerbViewDialog = (props) => {
  const classes = useStyles();
  const testament = (props.verbDialog.content.VerbView.refNum.substring(0,1) === 'G') ? 'NT' : 'OT'
  const dictionary = (testament === 'NT') ? require(`../../data/strongsGreekDictionary.json`) : (testament === 'OT') ? require(`../../data/strongsHebrewDictionary.json`) : null
  const DJHVerb = getVerbRef(props.verbDialog.content.VerbView.refNum)
  const strongLength = props.verbDialog.content.StrongNumber.length
  return (
    <Dialog 
      maxWidth='lg'
      open={props.verbDialog.open} 
      onClose={() => props.newVerbDialog({open: false, content: {}})}>
      <DialogTitle>
        <div className={classes.dialogHeader}>
            <img src={VerbViewLogo} className={classes.logo} alt="VerbViewLogo" />
            <Typography variant="h4" className={classes.title}>VerbView</Typography>
        </div>
        {`${props.verbDialog.content.text} : ${props.verbDialog.content.VerbView.refNum}`}
      </DialogTitle>
      <DialogContent dividers>
          <p className={classes.definition}>{`STRONG'S ${testament} #${props.verbDialog.content.StrongNumber.substring(1,strongLength)} DEFINITION:  `}<em>{(dictionary[props.verbDialog.content.StrongNumber]) ? dictionary[props.verbDialog.content.StrongNumber].strongs_def : 'N/A'}</em></p>
          <VerbWordDataTable content={props.verbDialog.content}/>
          <VerbCharts fileRef={DJHVerb} verbRef={props.verbDialog.content.VerbView.refNum} />
      </DialogContent>
    </Dialog>
  )
}

export default VerbViewDialog;