import React from 'react'
import styles from './Introduction.module.css'
import Paragraph from './Paragraph/Paragraph'
import BulletedList from './BulletedList/BulletedList'
import Table from './Table/Table'
import Subtitle from './Subtitle/Subtitle'
import IntroData from './IntroData/IntroData'

const Introduction = (props) => {
    return (
        <div>
            <div>
                {props.content.map(block => {
                    switch (block.type) {
                        case 'speech':
                            return <Paragraph content={block.nodes} />
                        case 'subtitle':
                            return <Subtitle content={block.nodes} />
                        case 'bulleted-list':
                            return <BulletedList content={block.nodes} />
                        case 'table':    
                            return <Table content={block.nodes}/>
                        default:
                            return null
                    }
                })}
            </div>
            {(props.segmentID === 'I042') && <div><IntroData book={props.book}/></div>}
        </div>
    )
}

export default Introduction