import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import TableRow from './TableRow'
import TableHeader from './TableHeader'

const useStyles = makeStyles(theme => ({
    table: {
        position: 'relative',
        width: '100%',
        borderCollapse: 'collapse',
        borderSpacing:0,
        display:'table',
        border: '1px solid #000',
        boxShadow:'0 4px 10px 0 rgba(0,0,0,0.2),0 4px 20px 0 rgba(0,0,0,0.19)',
    },
    tableTR: {
        border: 'none',
        borderBottom: '1px solid black',
        borderRight: '1px solid black',
    },
    tableTD: {
        border: '1px solid black',
        borderTop: 'none',
        borderBottom: 'none',
        borderRight: 'none',
        padding: '.5em',
        position: 'relative'
    }
  }))

const Table = (props) => {
    const classes = useStyles()
    let nodes = Array.from(props.content)
    nodes.shift()
    return (
        <table className={classes.table}>
          <tbody>
            <TableHeader content={Array.from(props.content)[0].nodes}></TableHeader>
            {nodes.map(row => <TableRow content={row.nodes}/>)}
          </tbody>
        </table>
    )
}

export default Table